import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { DragDropContext, Droppable } from 'react-beautiful-dnd'
import { Image } from 'react-bootstrap'

import Routine from './routine/Routine'
import Translate from '../components/common/Translate'

import { handleDragEnd } from '../utils'

function Header({ heading, text, image }) {
  return (
    <div className="fc-header">
      <div className="fc-header__container">
        <div className="fc-header__text">
          <div className="fc-header__text-heading">{heading}</div>
          <div className="fc-header__text-text">{text}</div>
        </div>
        {image && (
          <div className="fc-header__image">
            <Image src={process.env.PUBLIC_URL + '/images/' + image} fluid />
          </div>
        )}
      </div>
    </div>
  )
}

function Routines({ actions, routines }) {
  const history = useHistory()

  useEffect(() => {
    actions.requestRoutinesData()
  }, [actions])

  const setRoutinesCallback = (routines) => {
    actions.orderRoutines(routines)
  }

  const onRoutineClick = (routineId) => {
    history.push(process.env.PUBLIC_URL + '/routine/' + routineId)
  }

  const onDragEnd = (result) => {
    handleDragEnd(result, routines, setRoutinesCallback)
  }

  return (
    <div className="routines">
      <Header
        heading={<Translate lt="routines.header.heading" />}
        text={<Translate lt="routines.header.text" />}
        image="logo.png"
      />
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="list">
          {(provided) => (

            <div className="routines-list" ref={provided.innerRef} {...provided.droppableProps}>
              {routines.map((routine, index) => (
                <Routine
                  routine={routine}
                  index={index}
                  key={routine.id}
                  selectRoutine={onRoutineClick}
                />
              ))}
              {provided.placeholder}
            </div>

          )}
        </Droppable>
      </DragDropContext>
    </div>
  )
}

export default Routines
